@import url("./colorPallet.css");

.app{
  display: flex;
  justify-content:space-around;
  
  margin: 0;
  overflow: hidden;

  overflow-y: scroll;
  scroll-behavior: smooth;

  color: var(--font-color);

  font-size: 16px;
  font-weight: bold;
}

a{
  text-decoration: none;
  color: var(--font-color);
}

aside{
  margin: auto;
  height: 600px;
  width: 350px;
  padding: 50px 20px;
  box-sizing: border-box;


  margin-top: 10vh;
  border-radius: 25px;

  background-color: var(--post-bg-color);
  border: var(--inbetween-color) 2px solid;
  
  text-align: center;

  font-size: 1.1em;
}

aside p{
  margin: 20px;
}

aside h1, h2{

  margin-bottom: 50px;
}

aside h1{
  font-size: 2em;
}

.welcome{
  order: 1;
}

.links{
  order: 3;
}

.links ul{
  margin: 60px auto;
  padding: 0;
  list-style: none;

}

.links li{
  width: 100%;
  margin: 55px 0;
  padding: 20px 10px;
  font-size: 1.15em;
  background-color: #bcf2;
  border: var(--inbetween-color) 2px solid;
  border-radius: 15px;
  box-sizing: border-box;

  cursor: pointer;
  

  transition: all .25s;
}

.links li:hover{
  background-color: var(--post-bg-hover);
  scale:1.1;
}



.postSection{
  margin: 0;
  position:relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  order: 2;

  width: 650px;

  height: 100vh;

  color: var(--font-color)
}

.bedrock{
  margin: 0;
  padding: 50px 20px;
  background-color: var(--darker-post-color);
  color: var(--secondary-font-color);
  text-align: center;

  border: var(--inbetween-color) 1px solid;
  border-top: 0px;
}

.post{
  position: relative;
  padding: 20px;
  max-height: 225px;
  
  font-family: Helvetica, Arial, sans-serif;

  background-color: var(--post-bg-color);
  border: var(--inbetween-color) 1px solid;
  border-top: 0px;
}

.post:hover{
  background-color: var(--post-bg-hover);
}


.author{
  font-weight: 600;
  display: flex;
  justify-content: middle;
  padding: 10px;
  margin-bottom: 20px;
}

.author img{
  border-radius: 40px;
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin-right: 15px;
}

.author p{
  margin:auto 10px;
}

.text{
  margin:0 20px 50px 20px;
  word-wrap:break-word;
  line-height: 1.4;
  font-weight: 500;
}

.timestamp{
  float: right;
  position:absolute;
  margin: 0;

  font-size: 14px;
  font-weight: normal;
  color: var(--font-secondary-color);

  right: 20px;
  bottom: 20px;
  
}


.newPostBox{
  position: relative;
  padding: 30px 40px;
  min-height: 161px;
  background-color: var(--post-input-color);
  border: var(--inbetween-color) 1px solid;
  box-sizing: border-box;
}

.newPostBox p{
  margin: 0;
}

.postInput{
  display: block;
  margin: 40px auto 60px auto;

  padding: 10px;
  width: 96.5%;
  min-height: 20px;
  max-height: 160px;

  word-wrap: break-word;
  border-bottom: var(--inbetween-color) 1px solid;

  font-size: 20px;
  cursor: text;

}

[contenteditable]:empty::before {
  content: "Make a new post ...";
  color: var(--font-grayed-color);
}

.postInput:focus{
  outline: none;
}

.newPostBox .bottom{
  display: flex;
  justify-content: right;

  margin:0;

  width: 88%;

  position: absolute;
  bottom: 30px;
}

.charCountSection{
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 0 25px;
  user-select: none;
}

.barBack{
  position: relative;
  margin: auto 15px;
  padding: 0;
  height: 10px;
  width: 40px;

  border-radius: 4px;

  overflow: hidden;

  background-color: var(--inbetween-color);
}

.charCountBar{
  position: absolute;
  top:0;
  left:0;
  height: 10px;
}

.charCountBar.blue{
  background-color: var(--blogger-blue2);
}

.charCountBar.red{
  background-color: var(--blogger-red);
}

.button{
  background-color: var(--blogger-blue);
  padding: 7px 25px;
  border-radius: 25px;
  
  font-size: 17px;
  font-weight: 700;

  user-select: none;
  cursor: pointer;

  transition: all .15s;
}

.button:hover{
  scale: 1.1;
  filter: saturate(.9) brightness(1.2);
}

.button:active{
  filter:saturate(1.75) brightness(1);
  scale: .95;
}

.login{
  position: absolute;
  margin: -30px -40px;

  width: 100%;
  min-height: 100%;


  z-index: 1000;
}

.login span{
  margin: 0 10px;
}

.visible{
  visibility: visible;
  opacity: 1;

  backdrop-filter: blur(10px);

  transition: all .25s;
}

.hidden{
  opacity: 0;

  visibility: hidden;
  transition: all .1s;
}

.login form{
  display: flex;
  flex-direction: column;

  width: 40%;
  margin: auto;
}

.login label, input{
  margin-top:12px;
}

.login .bottom{
  bottom: 20px;
  width: 100%;
}